<script>
import moment from "moment";
export default {
  computed: {
    getNewsList: {
      get() {
        return this.$store.getters["userModule/getNewsList"];
      },
      set(data) {
        this.$store.commit("userModule/setNewsList", this.data);
      },
    },
    showNews: {
      get() {
        return this.$store.getters["adminModule/getshowNewsDialog"];
      },
      set(data) {
        this.$store.commit("adminModule/setShowNews", data);
      },
    },
  },
  beforeMount() {
    this.$store.commit("showProgressBar", {});
    this.$store
      .dispatch("userModule/getAllNews", { payload: { offset: 0, limit: 10 } })
      .then((response) => {
        this.len = 390;
        (this.limit = 1), (this.skip = 0), (this.heightCarousel = 350);
        if (response.data.result.length > 0) {
          this.empty = false;
          for (var i = 0; i < this.getNewsList.length; i++) {
            this.getNewsList[i].DATE = moment
              .unix(this.getNewsList[i].DATE / 1000)
              .format("LL");
          }
        } else {
          this.empty = true;
        }
      });

    this.$store.commit("closeProgressBar", {});
  },
  methods: {
    myFunction(data) {
      if (!this.showMore) {
        this.showMore = true;
        this.len = this.getNewsList[data].content.length;
        this.heightCarousel = 150 + this.len / 2;
      } else {
        this.showMore = false;
        this.len = 390;
        this.heightCarousel = 350;
      }
    },
    resetHeight() {
      this.heightCarousel = 350;
      this.showMore = false;
      this.len = 390;
    },
  },

  data() {
    return {
      limit: 1,
      skip: 0,
      heightCarousel: 550,
      len: 390,
      showMore: false,
      empty: false,
      colors: [
        "indigo",
        "pink darken-2",
        "red lighten-1",
        "deep-purple accent-4",
        "indigo",
        "pink darken-2",
        "red lighten-1",
        "deep-purple accent-4",
        "indigo",
        "pink darken-2",
        "red lighten-1",
        "deep-purple accent-4",
        "indigo",
        "pink darken-2",
        "red lighten-1",
        "deep-purple accent-4",
      ],

      event: [
        {
          id: 1,
          eventTitle: "Alumni Meet",
          eventDescription:
            "Lorem Ipsum is simply dummy text of the printing and typesetting industry.It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.",

          eventLocation: "New Delhi",
        },
        {
          id: 2,
          eventTitle: "Annual Sports News",
          eventDescription:
            "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.",
          eventLocation: "USA",
        },
        //  {
        //     id: 3,
        //     eventTitle: "Hello asdf",
        //     eventDescription: "AFGdnbvfbfjnbmkvmvckmdfvnfjvnjvjvnjcjjf",
        //     eventLocation: "USA",
        // },
        // {
        //     id: 4,
        //     eventTitle: "Hello asdf",
        //     eventDescription: "AFGdnbvfbfjnbmkvmvckmdfvnfjvnjvjvnjcjjf",

        //     eventLocation: "USA",
        // },
        // {
        //     id: 5,
        //     eventTitle: "Hello asdf",
        //     eventDescription: "AFGdnbvfbfjnbmkvmvckmdfvnfjvnjvjvnjcjjf",

        //     eventLocation: "USA",
        // },
      ],
    };
  },
};
</script>
<style >
#more {
  display: none;
}
div.item {
  vertical-align: top;
  display: inline-block;
  text-align: center;
}
.news-card
{
  background-color:#F7F7F7  !important;
  /* opacity:0.8  !important */
}
/* .news-carousel-wrapper .v-responsive__content {
  background: rgb(0, 0, 0, 0.5);
} */
.news-carousel-wrapper,.v-image
{
height: 700px;
}

@media screen and (max-width: 992px) {
  .news-carousel-wrapper,.v-image
{
height: 600px !important;
}
}

/* On screens that are 600px or less, set the background color to olive */
 @media screen and (max-width: 640px) {
 .news-carousel-wrapper,.v-image
{
height: 200px !important;
}
} 
</style>